@import "../_baby-variables.less";
@import "../_baby-mixins.less";

.fn-@{cms-ns}-baby-member{
	.swiper-container{
		padding: 2%;
	}

	.swiper-slide{
		width: 80%;
		margin-right: 2%;
		background: #fff;
		padding: 10px;

		.info{
			padding-left: 10px;
		}

		.detail{
			position: relative;
			line-height: 1.2;
			margin-top: 15px;
		}

		.title{
			display: block;
			.ellipsis(2);
			line-height: 1.2;
		}

		del{
			color: #999;
			text-decoration: line-through;
			font-size: @font-size-sm;
		}

		.btn{
			.absolute(_,0,0,_);
			color: #fff;
			background: @ht-color;
			padding: 5px 10px;
			border-radius: 4px;
		}
		.unable-btn{
			background: #868686;
		}
	}
}
