@import "../_cms-variables";
@import "../_cms-mixins";

/**************************************
***@head-height:
***@head-padding
***@left-width
***@img-width
*@img-hright
***@headline-img-url
***************************************/

.cmp-notice-nav(@head-height,@head-padding,@left-width,@img-width,@img-height,@headline-img-url){

	padding: unit(@head-padding, rem) 0;
	background: #fff;

	.pic{
		width:unit(@left-width, rem);
		float: left;
		text-align: right;
		padding-top:unit(@head-height,rem);
		position: relative;

		&:before{
			background: data-uri(@headline-img-url) no-repeat 0 0;
			.absolute(unit(@head-padding, rem),0,_,_);
			vertical-align: top;
			content: '';
			.size(unit(@img-width,rem),unit(@img-height, rem));
			background-size: 100% 100%;
		}
	}

	.list{
		margin-left: unit(@left-width, rem);
		padding-top:unit(@head-height,rem);
		position: relative;
		.headline-swiper{
			.absolute(0,_,_,0);
			.size(100%);

			.swiper-slide{
				a {
					display: block;
					margin-right:5px;
					line-height: 1.6;
					padding-left: 5px;
					.relative(50%,_,_,0);
					transform: translate(0,-50%);
					.ellipsis();
					color:#999;
				}
			}
		}
	}
}
