@import "../_baby-variables.less";
@import "../_baby-mixins.less";

.fn-@{cms-ns}-baby-birthday{
	.panel{
		background: #fff;
		padding: 10px;
	}

	.birth-item{
		.info{
			padding-left: 10px;
		}

		.title{
			display: block;
			.ellipsis(2);
		}

		.detail{
			padding-top: 5px;
		}

		del{
			color: #999;
			margin-left: 4px;
			font-size: @font-size-sm;
			text-decoration: line-through;
		}

		.btn{
			margin-top: 5px;
			display: inline-block;
			vertical-align: top;
			background: @ht-color;
			color: #fff;
			padding: 5px 10px;
			border-radius: 4px;
		}

		.unable-btn{
			background: #868686;
		}
	}

	.swiper-pagination{
		margin-top: 10px;
		height: .16rem;
		width: 100%;
		z-index: 5;
		text-align: center;

		.swiper-pagination-bullet{
			display: inline-block;
			vertical-align: top;
			text-align: center;
			color: #fff;
			border:.04rem solid #999;
			border-radius:100%;
			font-size: .16rem;
			height: .16rem;
			width: .16rem;
			margin-right: .2rem;

			&-active{
				background:@ht-color;
				border:.04rem solid @ht-color;
			}
		}
	}

	.panel-birth{
		border: 2px solid @baby-color;
		background: #fff;

		.panel-title{
			background: @baby-color;
			color: #fff;
			text-align: center;
			padding: 8px 0;

			i{
				margin-right: 6px;
			}
		}

		.panel-body{
			padding: 10px 8px;
		}
	}
}
