@import "../_baby-variables.less";
@import "../_baby-mixins.less";

.fn-@{cms-ns}-baby-record{
	padding: 10px;
	background: #FFF6EF data-uri('@{cms-image-dir}/@{baby-ns}/baby-record-bg.png') no-repeat right bottom;
	background-size: auto 80%;

	.record{
		display: table;
		width: 100%;

		.item{
			display: table-cell;
			vertical-align: middle;
		}

		.pic{
			.size(50px);
			background: data-uri('@{cms-image-dir}/@{baby-ns}/baby-girl-plan-bg.png') no-repeat 0 0;
			background-size: 100% 100%;
			position: relative;

			.mark{
				.absolute(_,_,0,0);
				.size(50px,20px);
				text-align: center;
				font-size:10px;
				.ellipsis();
				color:#d06350;
				background:data-uri('@{cms-image-dir}/@{baby-ns}/baby-plan-mark.png') no-repeat 0 0;
				background-size: 100% 100%;
			}
		}

		.baby{
			padding-left: 10px;
			line-height: 1.4;
			position: relative;

			i{
				font-size:16px;
			}

			select{
				.absolute(0,_,_,0);
				.size(100%);
				opacity: 0;
			}
		}

		.more{
			text-align: right;
			width: 150px;
			color: @baby-color;

			i{
				margin-left: 5px;
			}
		}
	}
}
