@import "../_baby-variables.less";
@import "../_baby-mixins.less";

.fc-m-coupon(@c1, @c2){
	.coupon-inner{
		background: @c1;
	}

	&:before,&:after{
		background-image: radial-gradient( transparent 0px, transparent 5px, @c1 5px, @c1);
	}

	.info{
		.expire{
			color: @c2;
		}
		.sub{
			color: @c2;

			.desc .get{
				background: @c2;
			}
		}
	}
}

.fn-@{cms-ns}-baby-coupon{
	background: #fff;

	.medal{
		display: table;
		padding: 10px;
		border-bottom: 1px solid #eee;
		width: 100%;

		.pic{
			width: 80px;
			display: table-cell;
			vertical-align: top;
			min-height: 1px;

			img{
				width: 100%;
				vertical-align: top;
			}
		}

		.info{
			padding-left:10px;
			.title{
				font-size: @font-size-lg;
				color: @baby-color;
			}

			.ht{
				color: #333;
			}

			p{
				color: #999;
			}
		}
	}

	.coupon-list{
		padding: 10px;

		.coupon{
			padding: 0 8px;
			position: relative;
			margin-top: 10px;

			&:first-child{
				margin-top: 0;
			}

			&:before,&:after{
				content: '';
				.absolute(0,_,_,_);
				.size(8px, 100%);
				background-image: radial-gradient( transparent 0px, transparent 5px, #CFAFCD 5px, #CFAFCD);
				background-size: 16px 12px;
				background-position: -9px 0px;
			}

			&:before{
				left: 0;
			}

			&:after{
				right: 0;
				background-position: 1px 0px;
			}

			.coupon-inner{
				background: #CFAFCD;
				display: table;
				width: 100%;
			}

			.pic{
				display: table-cell;
				vertical-align: middle;
				padding: 10px 10px 10px 5px;
				width: 100px;

				img{
					border-radius: 50%;
				}
			}

			.info{
				display: table-cell;
				vertical-align: top;
				padding: 5px 0;

				.title{
					color: #fff;
					font-size: @font-size-lg;
					margin-bottom: 2px;
				}

				.level{
					margin-top: 4px;
					color: #fff;
					font-size: @font-size-sm;
				}

				.expire{
					color: #984192;
					font-size: @font-size-sm;
				}

				.sub{
					color: #984192;
					overflow: hidden;

					.rmb{
						float: left;
					}

					.num{
						float: left;
						font-size: 44px;
						font-weight: bold;
						line-height: .8;
						font-family: arial;
						letter-spacing: -2px;
					}

					.desc{
						float: left;
						margin-left: 5px;
						font-size: @font-size-sm;
						line-height: 1.4;

						.get{
							color: #fff;
							background: #984192;
							display: inline-block;
							vertical-align: top;
							line-height: 1.6;
							padding: 0 5px;
						}
					}
				}
			}

			.txt{
				display: table-cell;
				vertical-align: middle;
				text-align: center;
				line-height: 1.1;
				padding: 10px 5px;
				color: #fff;
				border-left: 1px dashed #eee;
			}
		}

		.coupon-yellow{
			.fc-m-coupon(#F8AF4F, #AF4E00);
		}

		.coupon-green{
			.fc-m-coupon(#75D4BC, #0B8F6E);
		}

		.coupon-pink{
			.fc-m-coupon(#F56385, #AB1537);
		}

		.coupon-blue{
			.fc-m-coupon(#9FD3E8, #4288A4);
		}
	}
}
