@import "../_baby-variables.less";
@import "../_baby-mixins.less";

.baby-pic-grid(){
	.wrapper{
		position: relative;

		&:before{
			padding-top: 53%;
			content: '';
			display: block;
		}
	}

	.item{
		.absolute(_,_,_,_);

		&.img-box:before{
			padding-top:52.24%;
		}
		&-l{
			width: 49.3%;
			height: 100%;
			left: 0;
			top: 0;
		}

		&-rt{
			width: 49.3%;
			height: 48.6%;
			right: 0;
			top: 0;
		}

		&-rb{
			width: 49.3%;
			height: 48.6%;
			right: 0;
			bottom: 0;
		}
	}
}
