@import "../_baby-variables.less";
@import "../_baby-mixins.less";

.baby-item-list(){
	.item{
		padding: 10px 0;
		border-bottom: 1px solid #eee;

		&:last-child{
			border-bottom: none;

			.line-fix{
				display: none;
			}
		}
	}

	.pic{
		padding-right: 10px;
		position: relative;

		.mark{
			.absolute(-2px,_,_,10px);
			background: data-uri('@{cms-image-dir}/baby_recommon_mark.png') no-repeat 0 0;
			background-size: 100% 100%;
			z-index:2;
			.size(32px,27.5px);
			color:#fff;
			text-align: center;
			line-height: 26px;
			overflow: hidden;
			font-size: @font-size-sm;
		}
	}

	.line-fix{
		.absolute(_,_,-11px,0);
		.size(100%, 1px);
		background: #fff;
	}

	.info{
		padding-right: 10px;
	}

	.title{
		display: block;
		.ellipsis(2);
	}
}
